.container {
  margin: 0;
  padding: 0;
  position: relative;
  top: 0;
  left: 0;
  width: 50vw;
  height: 100vh;
  overflow: hidden;
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
}

.progress-ring {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: -1;
}

.progress-ring__circle {
  transition: stroke-dashoffset 0.35s;
  transform: rotate(-90deg);
  transform-origin: 50% 50%;
}

.text-message {
  position: absolute;
  bottom: 50px;
  text-align: justify;
  background: #ececec;
  padding: 8px 16px;
  border-left: 4px solid #555;
  padding-left: 4px;
}

input[type="text"] {
  border: none;
  outline: none;
  background: none;
  width: 48px;
  padding: 0;
  font-size: 40px;
  text-align: center;
}

input[type="text"][disabled] {
  color: #333;
}

button {
  position: relative;
  border: none;
  outline: none;
  background: none;
  cursor: pointer;
  padding: 0;
  margin: 2px 15px;
}

.tooltip {
  visibility: hidden;
  width: 85px;
  background-color: #555;
  color: #fff;
  text-align: center;
  border-radius: 6px;
  padding: 4px 0;
  position: absolute;
  z-index: 1;
  bottom: 125%;
  left: 50%;
  margin-left: -42.5px;
  opacity: 0;
  transition: opacity 0.3s;
}

.tooltip::after {
  content: "";
  position: absolute;
  top: 100%;
  left: 50%;
  margin-left: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: #555 transparent transparent transparent;
}

button:hover .tooltip {
  visibility: visible;
  opacity: 1;
}

.time-controllers {
  position: absolute;
  bottom: 50px;
}

.time-container {
  width: 300px;
  height: 300px;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}

.time-label {
  margin-right: 5px;
  font-size: 8px;
}

.vertical-divider {
  width: 4px;
  height: 90vh;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: black;
  border-radius: 2px;
}

.egg-container {
  position: absolute;
  top: 0;
  left: 50%;
  width: 50vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

@media only screen and (max-width: 700px) {
  .container {
    width: 100vw;
  }
  .vertical-divider {
    display: none;
  }
  .egg-container {
    display: none;
  }
}
